	module.exports = {"data":{"markdownRemark":{"html":"<p>This article covers step by step guide to install Google App Engine on your Fedora Operating System using python.</p>\n<ul>\n<li>Follow the steps in this documentation by <a href='https://cloud.google.com/appengine/docs/python/#uploading_the_application' target='_blank' rel='nofollow'>Google.</a></li>\n</ul>\n<p>Testing the application as given in the above document may not work for many.</p>\n<p>So, try <a href='http://stackoverflow.com/a/16970921' target='_blank' rel='nofollow'>this</a> as given by Brice Lin.</p>\n<p>Also, follow the deployment strategy as provided by Brice Lin. To do this open another terminal (if you wish).</p>\n<ul>\n<li>\n<p>Before deploying, you have to create the project in the Google Cloud Platform. Follow the steps from <a href='https://cloud.google.com/appengine/docs/python/#uploading_the_application' target='_blank' rel='nofollow'>Uploading the app</a></p>\n</li>\n<li>\n<p>But still following the above deployment strategy may not work for many. And an error like this may follow:</p>\n</li>\n</ul>\n<p> <code>ERROR appcfg.py:2396 An error occurred processing file '': HTTP Error 400: Bad Request Unexpected HTTP status 400\\. Aborting. Error 400: --- begin server output --- A version or backend parameter is required. --- end server output ---</code> </p>\n<p>This error occurs because of the missing Version statement in file <strong>app.yaml</strong>. So add <code>version: 1</code> in the <strong>app.yaml</strong> file in the application repository. Here, <code>helloworld</code> is the repository. Now it will work. Happy Coding &#x26; Making Application.</p>\n<p>Don’t forget to check this link out: <a href='https://www.youtube.com/watch?v=bfgO-LXGpTM' target='_blank' rel='nofollow'>Developing and deploying an application on Google App Engine on Youtube.</a></p>","fields":{"slug":"/miscellaneous/how-to-run-google-app-engine-in-fedora-using-python/"},"frontmatter":{"title":"How to Run Google App Engine in Fedora Using Python"}}},"pathContext":{"slug":"/miscellaneous/how-to-run-google-app-engine-in-fedora-using-python/"}}

